import React from "react";
import ListDescription from "./list_description";

class SummaryRow extends React.Component {
  render() {
    return (
      (!this.props.data.total) ? <tr>
        <td className="overflow-hidden w-1/12 px-5 py-3 border-b-2 overflow-hidden border-gray-200 bg-gray-100 text-center text-xs font-semibold text-gray-600 uppercase tracking-wider">
          {this.props.data.subject}
        </td>
        <td className="w-1/12 px-5 py-3 border-b-2 overflow-hidden border-gray-200 bg-gray-100 text-center text-xs font-semibold text-gray-600 uppercase tracking-wider"> {this.props.data.payed} "</td>
        <td className="w-1/12 px-5 py-3 border-b-2 overflow-hidden border-gray-200 bg-gray-100 text-center text-xs font-semibold text-gray-600 uppercase tracking-wider"> {parseFloat(this.props.data.date)?new Date((parseFloat(this.props.data.date))).toLocaleDateString():this.props.data.date} </td>
        <td className="w-1/12 px-5 py-3 border-b-2 overflow-hidden border-gray-200 bg-gray-100 text-center text-xs font-semibold text-gray-600 uppercase tracking-wider"> {this.props.data.status} </td>

        <td className="cursor-pointer hover:bg-gray-200 w-1/12 border-b-2 overflow-hidden border-gray-200 bg-gray-100 text-center text-xs font-semibold text-gray-600 uppercase tracking-wider"><button></button></td>
      </tr> :
        <tr className={this.props.data.mode===1?"bg-blue-300":this.props.data.mode===-1?"bg-red-100":"bg-blue-100"}>
          <th className="overflow-hidden  px-5 py-3 border-b-2 overflow-hidden border-gray-200 text-center text-xs font-semibold text-gray-600 uppercase tracking-wider">
          {parseFloat(this.props.data.msbTime)?new Date((parseFloat(this.props.data.msbTime))).toLocaleDateString():this.props.data.msbTime}</th>
          <th className="overflow-hidden  px-5 py-3 border-b-2 overflow-hidden border-gray-200 text-center text-xs font-semibold text-gray-600 uppercase tracking-wider">
          {this.props.data.payed} "</th>
          <th className="overflow-hidden  px-5 py-3 border-b-2 overflow-hidden border-gray-200 text-center text-xs font-semibold text-gray-600 uppercase tracking-wider">
          {this.props.data.status}</th>
          <th className="overflow-hidden  px-5 py-3 border-b-2 overflow-hidden border-gray-200 text-center text-xs font-semibold text-gray-600 uppercase tracking-wider">
            </th>
          <th className="overflow-hidden  px-5 py-3 border-b-2 overflow-hidden border-gray-200 text-center text-xs font-semibold text-gray-600 uppercase tracking-wider">
            </th>
        </tr>
    );
  }
}

export default SummaryRow;
